.middlepart{
    font-family: Arial, Helvetica, sans-serif;
}
  /* Float four columns side by side */
  .column {
    float: left;
    width: 300px;
    padding: 0 10px ;
    margin-left: 10px;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    height: auto;
    min-height: 88px;
    margin-right: 15px;
  }
  .youclass{
      box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
      width: auto;
      height: auto;
  }
  
  
  
 /* Remove extra left and right margins, due to padding */
.middlepart .row {margin: 0 -50px;}
 
/* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Responsive columns */
  @media  screen and (max-width:1000px) {
    .mlclass{
        margin: auto;
        margin-left: 100px;
    }
    .headpart{
        width: 50em;
    }
    .column {
        width: 100%;
        display: block;
        margin-bottom: 20px;
      }
}
  @media screen and (max-width: 600px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
    .mlclass{
        margin: auto;
        margin-left: 0px;
    }
    .youclass{
        margin-left: 0px;
    }
  }
 

